import React, {Component} from 'react'
import {List, DatePicker, WhiteSpace, Toast} from "antd-mobile";
import instance from "../../common/instance";
const Item = List.Item;
function formatDate(date) {
    const pad = n => n < 10 ? `0${n}` : n;
    const dateStr = `${date.getFullYear()}-${pad(date.getMonth() + 1)}`;
    return `${dateStr}`;
}
class Salary extends Component {
    constructor(props) {
        super(props);
        this.state = {
            baseInfo: this.getPrams(),
            date: new Date(),
            result: {}
        }
    }
    componentDidMount() {
        this.getSalary();
    }
    getPrams() {
        let wxparams = {};
        try {
            let pstr = window.sessionStorage.getItem("wxinfo") || "{}"
            wxparams = JSON.parse(pstr);
        } catch(e) {
            console.log(e);
        }
        return wxparams;
    }
    getSalary = async ()=>{
        Toast.loading("", 1);
        let {date, baseInfo} = this.state;
        let body = instance.post("/wx/salary/query", Object.assign({},baseInfo,{date}));
        let res = body.data || {};
        Toast.hide();
        if(res.code === "000") {
            this.setState({result: res.data || {}});
        } else {
            Toast.info(res.message || "");
        }
    }
    onChange=(date)=>{
        this.setState({date}, this.getSalary)
    }
    render() {
        let {result} = this.state;
        result = result || {};
        return (<div>
            <List className="date-picker-list" renderHeader={() => ''}  style={{backgroundColor: 'white'}}>
                <DatePicker
                    mode="month"
                    value={this.state.date}
                    format={val => `${formatDate(val)}`}
                    onChange={this.onChange}
                >
                    <List.Item arrow="horizontal"></List.Item>
                </DatePicker>
            </List>
            <WhiteSpace />
            <List renderHeader={() => ''}>
                <Item extra={result["fpa1006"] || 0}></Item>
                <Item extra={result["fpa15"] || 0}>	</Item>
                <Item extra={result["fpa17"] || 0}>	</Item>
            </List>
            <WhiteSpace />
            <List renderHeader={() => ''}>
                <Item extra={result["fpa19"] || 0}></Item>
                <Item extra={result["fpa1007"] || 0}></Item>
                <Item extra={result["fpa1008"] || 0}></Item>
                <Item extra={result["fpa1009"] || 0}></Item>
                <Item extra={result["fpa1010"] || 0}></Item>
                <Item extra={result["fpa1011"] || 0}></Item>
                <Item extra={result["fpa1012"] || 0}></Item>
                <Item extra={result["fpa1013"] || 0}></Item>
                <Item extra={result["fpa1014"] || 0}></Item>
                <Item extra={result["fpa1015"] || 0}></Item>
                <Item extra={result["fpa1016"] || 0}></Item>
                <Item extra={result["fpa1017"] || 0}>/</Item>
                <Item extra={result["fpa23"] || 0}></Item>
                <Item extra={result["fpa22"] || 0}></Item>
                <Item extra={result["fpa1018"] || 0}></Item>
                <Item extra={result["fpa1019"] || 0}></Item>
                <Item extra={result["fpa1020"] || 0}></Item>
                <Item extra={result["fpa1024"] || 0}></Item>
                <Item extra={result["fpa1021"] || 0}></Item>
                <Item extra={result["fpa1022"] || 0}></Item>
                <Item extra={result["fpa18"] || 0}></Item>
                <Item extra={result["fpa1023"] || 0}></Item>
            </List>
            <WhiteSpace size="md"/>
        </div>);
    }
}

export default Salary;


